.header {
    background: url(../img/cloud-top-mobile.png) bottom center no-repeat, url(../img/topBg.jpg) center top no-repeat;
    padding-bottom: 12rem;
    @include rmin($md) {
        padding-top: 6rem;
        background-image: url(../img/top-cloud.png), url(../img/topBg.jpg);
        background-position: 40% bottom, center top;
        background-attachment: auto, fixed;
        background-repeat: no-repeat, no-repeat; }
    @include rmin($lg) {
        background-position: center bottom, center top; }
    &__wrap {
        position: relative; }
    &__text {
        display: flex;
        justify-content: flex-end;
        position: relative;
        z-index: 9; }
    &__right {
        position: relative;
        z-index: 9;
        width: 100%;
        @include rmin($md) {
            max-width: 70%;
            padding-right: 2rem; }
        @include rmin($lg) {
            max-width: 55%; } }
    &__description {
        padding-top: 2rem;
        color: #c1d2db;
        font-size: 1.125rem;
        @include rmin($md) {
            padding-top: 3rem; } }
    &__group {
        display: flex;
        align-items: center;
        padding-top: 1.875rem;
        &-text {
            width: 9rem;
            margin-right: 1rem;
            & p {
                color: $white;
                text-transform: uppercase;
                font-weight: 500; } } }
    &__mobile {
        display: flex;
        padding-top: 1rem;
        justify-content: space-between;
        @include rmin($md) {
            display: none; } } }


.navigation {
    position: fixed;
    display: none;
    z-index: 10;
    width: 100%;
    padding: 2rem 0;
    @include tr(all ease 0.25s);
    @include rmin($md) {
        display: flex; }
    &.is-scroll {
        background: #39273b;
        padding: 1.25rem 0; }
    &__wrap {
        max-width: 820px;
        margin: 0 auto;
        display: none;
        justify-content: space-between;
        align-items: center;
        @include rmin($md) {
            display: flex; } }
    &__items {
        display: flex;
        flex-wrap: wrap; }
    &__item {
        padding: 0 1rem 0 0;
        @include rmin($lg) {
            padding: 0 2rem 0 0; } }
    &__link {
        display: block;
        color: $white;
        text-decoration: none;
        font-size: 0.750rem;
        position: relative;
        @include tr(all ease 0.25s);
        &::after {
            position: absolute;
            left: 0;
            bottom: -5px;
            height: 2px;
            background: $orange;
            content: ' ';
            width: 0;
            @include tr(width ease 0.25s); }
        &:hover {
            color: $orange; }
        &:hover::after {
            width: 100%; } }
    &__item.is-active &__link {
        color: $orange; }
    &__item.is-active &__link::after {
        width: 100%; }

    &__contact {
        display: flex;
        align-items: center; }
    &__phone {
        width: 1.25rem;
        display: block;
        margin-left: 1.25rem;
        border: none; }
    &__mail {
        width: 1.25rem;
        display: block;
        margin-left: 1.25rem;
        border: none; } }


.burger {
    position: fixed;
    right: 0;
    top: 0;
    padding: 0.5rem 0.75rem;
    z-index: 10;
    background: #2b0f2f;
    &__link {
        display: block;
        width: 24px;
        & span {
            height: 3px;
            background: #ffffff;
            margin: 5px 0;
            display: block; } } }
.logo-mobile {
    width: 25%;
    display: block; }

/* ABOUT */
.about {
    position: relative;
    margin-top: -16rem;
    margin-bottom: -17rem;
    padding-top: 14rem;
    padding-bottom: 14rem;
    @include rmin($sm) {
        margin-top: -14rem; }
    &__lay {
        background: #ffffff; }
    &__text-block {
        max-width: 820px;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 2rem; }
    &__left {
        width: 100%; // 16.125rem
        display: block;
        @include rmin($md) {
            width: 16.125rem; } }
    &__right {
        width: 100%;
        padding-top: 2.5rem;
        margin-top: 1rem;
        background: url(../img/svg/dots.svg) -1rem 0.75rem no-repeat;
        background-size: 6.5rem;
        @include rmin($md) {
            width: calc(100% - 16.125rem);
            padding-left: 5.5rem;
            padding-top: 0;
            margin-top: 0.5rem;
            background: url(../img/svg/dots.svg) -1rem 0.75rem no-repeat;
            background-size: 6.5rem; }
        & p {
            padding-bottom: 1rem;
            color: #260629;
            @include rmin($md) {
                padding-left: 1rem; } } }
    &__callback {
        display: none;
        @include rmin($md) {
            display: block;
            margin-top: 2.5rem; } }
    &__mcallback {
        display: block;
        @include rmin($md) {
            display: none; } }
    &__question {
        color: $orange;
        &--top {
            margin-top: 1.5rem; } }
    &__btn {
        margin-top: 2rem; } }

/* advantages */
.advantages {
    text-align: center;
    padding-top: 12rem;
    padding-bottom: 2rem;
    background: url(../img/cloud2.png) 35% 0 no-repeat,url(../img/field.png) center top no-repeat #250628;
    background-size: 60rem, 95%;
    @include rmin($md) {
        background-image: url(../img/cloud2.png),url(../img/field.png);
        background-color: #250628;
        background-repeat: no-repeat, no-repeat;
        background-position: center -2.5rem, center top;
        background-size: auto,auto;
        padding-bottom: 10rem;
        padding-top: 14rem; }
    &__wrap {
        position: relative;
        z-index: 3; }
    &__items {
        margin-top: 2rem;
        display: flex;
        flex-wrap: wrap;
        @include rmin($md) {
            margin-top: 8rem; } }
    &__item {
        width: 100%;
        padding: 0 1rem;
        margin-bottom: 2rem;
        @include rmin($md) {
            max-width: 50%; }
        @include rmin($lg) {
            max-width: 25%; } }
    &__icon {
        width: 5rem;
        height: 8rem;
        fill: $orange;
        @include rmin($md) {
            height: auto; }
        &--fix {
            width: 3.5rem; } }
    & p {
        color: $white; } }


/* RED BLOCK */
.red-block {
    background: $red;
    padding: 2.5rem 0;
    &__wrap {
        position: relative;
        max-width: 820px;
        margin: 0 auto; }
    &__title {
        font-size: 1.375rem;
        line-height: 1.8;
        max-width: 75%;
        color: #ffffff;
        font-family: 'Roboto Slab', serif;
        @include rmin($md) {
            max-width: 75%;
            font-size: 2.375rem;
            line-height: 1.1; } }

    &__cup {
        position: absolute;
        width: 5.5rem;
        right: 0;
        top: -5rem;
        @include rmin($sm) {
            width: 7rem;
            right: 0rem;
            top: -5rem; }
        @include rmin($md) {
            width: 10rem;
            right: -3rem;
            bottom: -3.75rem;
            top: auto; } }
    &__confeti {
        position: absolute;
        width: 30rem;
        right: -7rem;
        top: -7rem;
        // width: 46rem
        // right: -15rem
 } }        // top: -15rem

/* Values */
.values {
    padding: 2rem 0;
    @include rmin($md) {
        padding: 5rem 0; }
    & .container {
        background: url(../img/svg/cosmonaut.svg) no-repeat top 4rem center;
        background-size: 17rem;
        @include rmin($sm) {
            background: url(../img/svg/cosmonaut.svg) no-repeat top 4rem center;
            background-size: 20rem; }
        @include rmin($md) {
            background: url(../img/svg/cosmonaut.svg) no-repeat top 5rem right 0;
            background-size: 20rem; }
        @include rmin($lg) {
            background: url(../img/svg/cosmonaut.svg) no-repeat top 3rem right 0;
            background-size: 24rem; } }

    &__wrap {
        max-width: 820px;
        margin: 0 auto; }
    &__left {
        max-width: 100%;
        @include rmin($md) {
            max-width: 400px; }
        @include rmin($lg) {
            max-width: 550px; } }
    &__items {
        padding-top: 18rem;
        margin-top: 2rem;
        margin-bottom: 2rem;
        @include rmin($sm) {
            padding-top: 21rem; }
        @include rmin($md) {
            padding-top: 0; } }
    &__item {
        color: #260629;
        padding-bottom: 0.75rem;
        padding-left: 0.75rem;
        position: relative;
        &::after {
            position: absolute;
            content: '-';
            left: 0;
            top: 0; } } }

/* Trainer */
.trainer {
    margin-top: 1rem;
    margin-bottom: 2rem;
    &__wrap {
        max-width: 820px;
        margin: 0 auto; }
    &__slider {
        margin-top: 4rem;
        @include rmin($lg) {
            padding-left: 5rem; } }
    &__item {
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        justify-content: center;
        text-align: center;
        @include rmin($lg) {
            justify-content: center;
            text-align: left; } }
    &__photo {
        width: 100%;
        max-width: 15.625rem;
        margin-bottom: 3rem; }
    &__text {
        width: 100%;
        @include rmin($lg) {
            width: calc(100% - 15.625rem);
            padding-left: 3rem; } }
    &__fio {
        font-size: 1.75rem;
        font-family: 'Roboto Slab', serif;
        line-height: 1.1;
        color: #250629;
        margin-bottom: 1rem;
        @include rmin($lg) {
            font-size: 3.125rem; } }
    &__position {
        color: #8589a3;
        font-size: 1.563rem;
        line-height: 1.2;
        margin: 1.5rem 0;
        @include rmin($md) {
            font-size: 1.563rem;
            margin: 0 0 1rem 0;
            line-height: inherit; } }
    &__description {
        color: #39273b;
        font-size: 1rem;
        @include rmin($md) {
            font-size: 1.125rem; }
        & p {
            margin-bottom: 0.75rem; } } }


/* Stadium */
.stadium {
    &__wrap {
        max-width: 900px;
        margin: 0 auto;
        background: $white;
        border-radius: 15px;
        padding: 11rem 1rem 1.5rem 1rem;
        display: flex;
        position: relative;
        box-shadow: 0 0 30px 1px rgba(38, 6, 41,0.1);
        @include rmin($md) {
            padding: 13rem 2rem 2rem 2rem; }
        @include rmin($lg) {
            padding: 4rem 3rem;
            justify-content: flex-end; } }
    &__right {
        display: inline-block; }
    &__lists {
        margin-top: 3rem; }
    &__list {
        margin: 0.75rem 0;
        color: #8589a3;
        font-size: 1.25rem;
        @include rmin($md) {
            font-size: 1.563rem; } }
    &__adres {
        color: $orange;
        font-size: 1.25rem;
        font-family: 'Roboto Slab', serif;
        margin-top: 1rem; }
    &__stadium {
        position: absolute;
        top: -1rem;
        left: 1rem;
        width: 12rem; //19rem
        @include rmin($md) {
            width: 16rem; }
        @include rmin($lg) {
            width: 19rem;
            top: -1rem;
            left: -1rem; }
        & img {
            width: 100%; } }
    &__confeti {
        position: absolute;
        top: -10rem;
        left: -2rem;
        width: 17rem;
        @include rmin($md) {
            width: 24rem;
            top: -12.5rem;
            left: -3.5rem; }
        & img {
            width: 100%; } } }

/* Method */
.method {
    background: #250628;
    padding-top: 17rem;
    padding-bottom: 4rem;
    margin-top: -12.5rem;
    @include rmin($md) {
        padding-bottom: 1rem; }
    &__wrap {
        max-width: 820px;
        margin: 0 auto; }
    &__slider {
        max-width: 900px;
        margin: 3rem auto;
        padding: 15px;
        border: 1px solid $orange;
        @include rmin($md) {
            padding: 2.5rem 2.25rem 2rem; } }
    &__item {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between; }
    &__col {
        width: 100%;
        @include rmin($md) {
            width: 47%; } }
    &__text {
        color: #e0eef2;
        font-size: 1rem;
        margin-bottom: 1.5rem;
        line-height: 1.4;
        @include rmin($md) {
            font-size: 1.125rem; } } }


/* Benefit */
.benefit {
    background: #250628;
    &__wrap {
        max-width: 820px;
        margin: 0 auto;
        background: url(../img/svg/cosmonaut2.svg) no-repeat center 7rem;
        //background-size: 18.125rem
        background-size: 17rem;
        padding-bottom: 10rem;
        @include rmin($md) {
            padding: 7rem 0 10rem;
            background: url(../img/svg/cosmonaut2.svg) no-repeat right top;
            background-size: 22rem; } }
    &__description {
        color: #8589a3;
        padding-top: 19rem;
        padding-bottom: 3.875rem;
        max-width: 31.25rem;
        text-align: center;
        @include rmin($md) {
            padding-top: 2.5rem;
            text-align: left; } } }


/* Footer */
.footer {
    background: url(../img/footer-cloud-mobile.png) no-repeat center bottom $red;
    position: relative;
    margin-top: -3rem;
    @include rmin($md) {
        margin-top: -7rem;
        padding-top: 4.5rem;
        background: url(../img/footer-cloud.png) no-repeat center bottom, url(../img/footer-bg.png) no-repeat center top; }
    &::before {
        background: url(../img/footer-bg.png) no-repeat center top;
        background-size: 100%;
        content: ' ';
        position: absolute;
        top: -2rem;
        height: 2rem;
        width: 100%;
        @include rmin($md) {
            background: transparent; } }
    &__wrap {
        display: flex;
        justify-content: flex-end;
        //background: url(../img/svg/raketa.svg) no-repeat -7rem 2rem
        background-size: 27rem;
        padding-top: 3rem;
        padding-bottom: 14rem;
        @include rmin($md) {
            padding-top: 4rem; } }
    &__right {
        width: 100%;
        @include rmin($md) {
            max-width: 58.125rem;
            padding-left: 5rem; }
        @include rmin($lg) {
            max-width: 58.125rem;
            padding-left: 10rem; } }
    &__lists {
        margin-top: 2rem;
        counter-reset: heading;
        @include rmin($md) {
            columns: 2;
            margin-top: 4rem; } }
    &__list {
        color: $white;
        margin-bottom: 1rem;
        padding-left: 1.25rem;
        line-height: 1.4;
        position: relative;
        &::before {
            counter-increment: heading;
            position: absolute;
            font-weight: 500;
            left: 0;
            color: $white;
            content: counter(heading) ". "; } } }

/* Subscribe */
.subscribe {
    //background: url(../img/footer-cloud.png) no-repeat center top
    background: #ffffff;
    padding-top: 2rem;
    padding-bottom: 3rem;
    @include rmin($md) {
        padding-top: 0; }
    &__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: center; }
    &__slogan {
        color: $red;
        font-size: 1.5rem;
        font-family: 'Roboto Slab', serif;
        width: 100%;
        text-align: center;
        @include rmin($md) {
            font-size: 1.875rem; } } }

.social {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    &__text {
        font-size: 1.375rem;
        line-height: 1.2;
        color: #8589a3;
        width: 100%;
        text-align: center;
        margin-bottom: 3rem;
        @include rmin($md) {
            margin-right: 5rem;
            width: auto;
            line-height: 1.6; } }
    &__icons {
        display: flex;
        margin-bottom: 3rem; }
    &__icon {
        display: block;
        margin: 0 .75rem;
        width: 2.625rem;
        @include tr(all ease 0.25s);
        &:hover {
            opacity: 0.7; } } }


.is-overflow {
    overflow: hidden;
    height: auto; }
/* Mobile navigation */
.mobile-navigation {
    overflow: hidden;
    z-index: 99;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: -100%;
    transform: translateZ(0);
    backface-visibility: hidden;
    @include tr(transform ease 0.25s);
    @include rmin($md) {
        display: none; }
    &__lay {
        bottom: 0;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        overflow: auto; }
    &__inner {
        width: 100%;
        background: url(../img/mobile-menu-img.jpg) no-repeat center top #ffffff;
        padding-top: 16rem; }
    &.is-open {
        transform: translateX(100%); }
    &__items {
        margin: 0 0 3rem 0; }
    &__item {
        text-align: center;
        padding: 0.5rem 0; }
    &__link {
        font-size: 1.375rem;
        color: #250628;
        text-decoration: none; }
    &__contact {
        font-size: 2rem;
        font-family: 'Roboto Slab', serif;
        text-align: center;
        display: block;
        text-decoration: none;
        color: #260629;
        & span {
            color: $orange;
            font-family: 'Roboto Slab', serif; } }
    &__social {
        margin-top: 3rem; }
    &__close {
        position: absolute;
        top: 0.5rem;
        right: 0.75rem;
        width: 25px;
        display: block; }
    &__link {
        position: relative;
        display: block;
        cursor: pointer;
        height: 25px;
        & span {
            position: absolute;
            top: 50%;
            width: 100%;
            height: 3px;
            background: #ffffff;
            display: block;
            &:first-child {
                transform: rotate(45deg); }
            &:last-child {
                transform: rotate(-45deg); } } } }



/* MODAL STYLE */
.remodal-close, .remodal-close:before {
    right: 0;
    left: auto; }
.remodal {
    &__success {
        display: none;
        background: #23c254;
        padding: 0.75rem 1rem;
        margin-top: 2rem;
        & p {
            color: $white;
            text-align: center; } } }

.form {
    &__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 2rem; }
    &__control {
        border: 2px solid #cccccc;
        font-size: 1.25rem;
        padding: 0.5rem;
        width: 100%;
        @include tr(border ease 0.25s);
        &:focus {
            border: 2px solid $orange; } }
    &__input {
        margin-bottom: 2rem;
        width: 100%;
        @include rmin($md) {
            width: 47%; } } }
