/* Mobile navigation */
.mobile-navigation {
  overflow: hidden;
  z-index: 99;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: -100%;
  transform: translateZ(0);
  backface-visibility: hidden;
  transition: transform ease 0.25s;
}

@media only screen and (min-width: 768px) {
  .mobile-navigation {
    display: none;
  }
}

.mobile-navigation__lay {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  overflow: auto;
}

.mobile-navigation__inner {
  width: 100%;
  background: url(../img/mobile-menu-img.jpg) no-repeat center top #ffffff;
  padding-top: 16rem;
}

.mobile-navigation.is-open {
  transform: translateX(100%);
}

.mobile-navigation__items {
  margin: 0 0 3rem 0;
}

.mobile-navigation__item {
  text-align: center;
  padding: 0.5rem 0;
}

.mobile-navigation__link {
  font-size: 1.375rem;
  color: #250628;
  text-decoration: none;
}

.mobile-navigation__contact {
  font-size: 2rem;
  font-family: 'Roboto Slab', serif;
  text-align: center;
  display: block;
  text-decoration: none;
  color: #260629;
}

.mobile-navigation__contact span {
  color: #f6921e;
  font-family: 'Roboto Slab', serif;
}

.mobile-navigation__social {
  margin-top: 3rem;
}

.mobile-navigation__close {
  position: absolute;
  top: 0.5rem;
  right: 0.75rem;
  width: 25px;
  display: block;
}

.mobile-navigation__link {
  position: relative;
  display: block;
  cursor: pointer;
  height: 25px;
}

.mobile-navigation__link span {
  position: absolute;
  top: 50%;
  width: 100%;
  height: 3px;
  background: #ffffff;
  display: block;
}

.mobile-navigation__link span:first-child {
  transform: rotate(45deg);
}

.mobile-navigation__link span:last-child {
  transform: rotate(-45deg);
}

