@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&subset=cyrillic');
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:700&subset=cyrillic');

$white: #ffffff;
$orange: #f6921e;
$red: #da2128;

$sm: 576;
$md: 768;
$lg: 992;
$xl: 1200;

html {
    font-size: 15px;
    line-height: 1.5;
    @include rmin(340) {
        font-size: 16px; } }
body {
	background-color: #f0f2ff; }
body {
    overflow-x: hidden; }

* {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    box-sizing: border-box; }

a img {
    border: none; }

.out {
    overflow-x: hidden; }
.container {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    padding: 0 20px;
    @include rmin($md) {
        max-width: 720px; }
    @include rmin($lg) {
        max-width: 900px; }
    @include rmin($xl) {
        max-width: 1040px; } }

.btn {
    background: $orange;
    font-size: 1rem;
    line-height: 1rem;
    padding: .5rem 1.563rem .65rem;
    display: inline-block;
    border-radius: 20px;
    color: $white;
    text-decoration: none;
    border: 1px solid transparent;
    cursor: pointer;
    @include tr(all ease 0.25s);
    &--big {
        padding: 0.65rem 1.563rem 0.75rem;
        border-radius: 25px; }
    &:hover {
        background: transparent;
        color: $orange;
        border: 1px solid $orange; } }


.title {
    font-family: 'Roboto Slab', serif;
    font-size: 2rem;
    line-height: 1.1;
    color: #250629;
    @include rmin($md) {
        font-size: 2.375rem; }
    &--white {
        color: #ffffff; }
    &--red {
        color: #da2128;
        font-size: 1.875rem; } }


/* Trainer dots */
.trainer__slider .owl-dots {
    position: absolute;
    top: -6rem;
    right: 0rem;
    display: inline-block;
    flex-flow: column;
    @include rmin($lg) {
        position: absolute;
        top: 3rem;
        left: 2rem;
        display: flex;
        flex-flow: column; } }

.trainer__slider .owl-dot {
    width: 0.63rem;
    height: 0.63rem;
    margin: 0 0.25rem;
    border-radius: 10px;
    background: #231f20;
    @include tr(all ease 0.2s);
    @include rmin($lg) {
        margin: 0.63rem 0; }
    &.active {
        background: $orange; } }


.method__slider .owl-dots {
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: -3rem;
    right: 0;
    left: 0;
    @include rmin($md) {
        position: relative;
        bottom: 0; } }

.method__slider .owl-dot {
    width: 0.63rem;
    height: 0.63rem;
    margin: 0 0.5rem;
    border-radius: 10px;
    background: #8eb1c2;
    @include tr(all ease 0.2s);
    &.active {
        background: $orange; } }





/* Tooltip container */
.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black; } //If you want dots under the hoverable text

/* Tooltip text */
.tooltip .tooltiptext {
    font-family: 'Roboto Slab', serif;
    visibility: hidden;
    opacity: 0;
    width: 180px;
    background-color: black;
    color: #000000;
    text-align: center;
    padding: 3px 0;
    border-radius: 6px;
    text-decoration: none;
    /* Position the tooltip text - see examples below! */
    position: absolute;
    z-index: 1;
    background: #ffffff;
    @include tr(opacity ease 0.2s); }
.tooltip .tooltiptext span {
    font-family: 'Roboto Slab', serif;
    color: $orange; }

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1; }
.tooltip-bottom {
    top: 135%;
    left: 50%;
    margin-left: -90px; }
.tooltip-bottom::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent #ffffff transparent; }


.preload {
    position: fixed;
    display: none;
    align-items: center;
    justify-content: center;
    background: #ffffff;
    z-index: 999999;
    width: 100%;
    height: 100%;
    animation-name: preload;
    animation-duration: 1.5s;
    animation-fill-mode: both;
    animation-delay: 0.5s;
    @include rmin($md) {
        display: flex; }
    &::after {
        content: ' ';
        position: absolute;
        left: -25%;
        bottom: -22.5rem;
        background: url(../img/cloud2.png) center bottom no-repeat;
        background-size: cover;
        width: 150%;
        height: 23rem; }
    &__img {
        display: block;
        width: 10rem;
        animation-name: preload__img;
        animation-duration: 2s;
        animation-fill-mode: both; } }

@keyframes preload__img {
    from {
        transform: translate3d(0, 200%, 0); }
    to {
        transform: translate3d(0, 0, 0); } }

@keyframes preload {
    from {
        transform: translate3d(0, 0, 0); }
    to {
        transform: translate3d(0, -150%, 0); } }

/* Animation */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translate3d(0, 100%, 0); }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0); } }

.fadeInUp {
    animation-name: fadeInUp;
    animation-duration: 1s;
    animation-fill-mode: both; }

@keyframes fadeIn {
    from {
        opacity: 0; }
    to {
        opacity: 1; } }

.fadeIn {
    animation-name: fadeIn;
    animation-duration: 1s;
    animation-fill-mode: both; }


@keyframes cloud-top {
    from {
        background-position: 0 bottom, center top; }
    to {
        background-position: 0 bottom, center top; } }
